define("ember-collapsible-layout-demo/mirage/fixtures/cats", ["exports"], function (exports) {
  var cats = [];

  for (var i = 1; i < 19; i++) {
    cats.push({
      id: i,
      type: "cats",
      attributes: {
        url: "/images/cats/" + i + ".jpg",
        title: "Cat #" + i
      }
    });
  }

  exports["default"] = cats;
});